<template>
  <div class="main">
    <!-- 内容区域1 -->
    <div class="main-content" v-for="item in arriPad" :key="item.id">
      <div class="content1">
        <div>
          <span>{{ item.title }}</span>
          <span>{{ item.text }} </span>
          <span @click="Buybtn(item.number)">{{ item.btn }}</span>
          <span>{{ item.link }}></span>
        </div>
        <img :src="this.$store.state.ipUrl + item.imageUrl" alt="" />
      </div>
      <div class="space-line"></div>
    </div>
    <!-- 内容区域2 -->
    <div class="main-content2">
      <div class="content2-title">Which iPad is right for you?</div>
      <section>
        <div class="content2" v-for="item in arriPad2" :key="item.id">
          <img :src="this.$store.state.ipUrl + item.imageUrl" alt="" />
          <div>
            <span>{{ item.title }}</span>
            <span>{{ item.text }} </span>
            <span @click="Buybtn(item.number)">{{ item.btn }}</span>
            <span>{{ item.link }}></span>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>
<script>
import { iPadcontent, iPadcontent2 } from '../url/dataUrl'
export default {
  data: function () {
    return {
      // 内容区域1数组
      arriPad: [],
      // 内容区域2数组
      arriPad2: [],
      number: null
    }
  },
  mounted () {
    this.Maincontent()
    this.Maincontent2()
  },
  methods: {
    //  获取内容区域1的api
    async Maincontent () {
      const { data: res } = await iPadcontent()
      this.arriPad = res

    },
    //  获取内容区域2的api
    async Maincontent2 () {
      const { data: res } = await iPadcontent2()
      console.log(res)
      this.arriPad2 = res
    },
    Buybtn (nub) {
      this.$router.push({ path: 'Buy', query: { nub: nub } })
    }
  }
}
</script>
