<template>
  <div class="Main">
    <div class="content-left">
      <el-carousel
        indicator-position="outside"
        height="440px"
        :loop="false"
        :interval="1000"
      >
        <el-carousel-item v-for="item in main.image" :key="item.id">
          <img :src="this.$store.state.ipUrl + item" alt="" />
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="content-right">
      <div class="content-title">
        <h2>{{ main.title }}</h2>
        <p>
          {{ main.message }}
        </p>
        <p>{{ main.price }}</p>
      </div>
      <div class="location-add">
        <span>{{ main.address }} </span>
        <span>修改</span>
        <br />
        <span>{{ main.ishave }}</span>
      </div>
      <div class="line-crose"></div>
      <el-form
        ref="form"
        :style="{ backgroundColor: '#f1f1f1' }"
        :model="form"
        label-width="120px"
      >
        <el-form-item label="选择版本">
          <el-radio-group v-model="form.version">
            <el-radio
              v-for="item in main.selectVersion"
              :key="item.id"
              :label="item"
            ></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="选择颜色">
          <el-radio-group v-model="form.color">
            <el-radio
              v-for="item in main.selectColor"
              :key="item.id"
              :label="item"
            ></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="选择服务">
          <el-radio-group v-model="form.serve">
            <el-radio
              v-for="item in main.selectServe"
              :key="item.id"
              :label="item"
            ></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item>
          <el-button class="go-buy" type="primary" @click="onSubmit"
            >立即购买</el-button
          >
          <el-button class="go-cart">加入购物车</el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-dialog
      v-model="centerDialogVisible"
      title="兄弟请付款"
      width="40%"
      center
    >
      <div class="content-dialog">
        <img src="../data/img/getmoney.jpg" alt="" />
        <span>{{ main.title }}</span>
        <span>版本:{{ form.version }}</span>
        <span>颜色:{{ form.color }}</span>
        <span>服务:{{ form.serve }}</span>
        <span>地址:{{ main.address }}</span>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="centerDialogVisible = false">已付款</el-button>
          <el-button type="primary" @click="centerDialogVisible = false"
            >取消</el-button
          >
        </span>
      </template>
    </el-dialog>
  </div>
</template>
<script>
import { Buycontent1 } from '../url/dataUrl'
export default {
  data: function () {
    return {
      form: {
        version: '',
        color: '',
        serve: ''
      },
      main: {},
      centerDialogVisible: false,
      number: this.$route.query.nub
    }
  },
  mounted () {
    this.Main()
    document.documentElement.scrollTop = 0
  },
  methods: {
    onSubmit () {
      this.centerDialogVisible = true
      //   this.$message({
      //     message: this.form,
      //     type: 'success'
      //   })
    },
    async Main () {
      const { data: res } = await Buycontent1(this.number)
      console.log(res[0])
      this.main = res[0]
    }
  }
}
</script>
<style lang="scss" scoped>
.Main {
  width: 1480px;
  display: flex;
  justify-content: space-around;

  .content-left {
    width: 500px;
    .el-carousel {
      width: 500px;
      img {
        width: 500px;
      }
    }
  }
  .content-right {
    width: 500px;
    .content-title {
      h2 {
        height: 40px;
        line-height: 40px;
        display: block;
      }
      p {
        height: 50px;
        display: block;
        font-size: 13px;
        color: red;
      }
    }

    .location-add {
      span:nth-child(2) {
        margin-left: 20px;
        color: orange;
      }
      span:nth-child(2):hover {
        cursor: pointer;
        color: orangered;
      }
      span:nth-child(4) {
        color: orange;
      }
    }
    .el-form-item {
      width: 500px;
    }
    .go-buy {
      background-color: #ff6700;
    }
    .el-radio-group {
      width: 380px;
    }
  }
  .content-dialog {
    img {
      width: 300px;
      height: 400px;
    }
    span {
      display: block;
    }
  }
}
@media screen and(max-width:1024px) {
  .Main {
    width: 1024px;
  }
}
</style>