  mounted () {
    this.Mac1content1()
    this.Mac1content2()
    this.Mac1content3()
  },
  methods: {
    //  获取内容区域1的api
    async Mac1content1 () {
      const { data: res } = await Maccontent1()
      this.arrMac1 = res

