    async Mac1content3 () {
      const { data: res } = await Maccontent3()
      console.log(res)
      this.arrMac3 = res
    },
    Buybtn (nub) {
      this.$router.push({ path: 'Buy', query: { nub: nub } })
    }
  }
}
