<template>
  <div class="login">
    <div class="loginview">
      <h1>请登录。</h1>
      <!-- 登录表单 -->
      <el-form
        ref="form"
        :model="form"
        :rules="rules"
        label-width="120px"
        class="demo-ruleForm"
      >
        <!-- 用户名 -->
        <el-form-item label="Shop_用户" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item label="Shop_密码" prop="paw">
          <el-input
            v-model="form.paw"
            placeholder="Please input password"
            show-password
          />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="open">Create</el-button>
          <el-button>Cancel</el-button>
        </el-form-item>
      </el-form>
      <div class="login-space"></div>
      <!-- 创建用户 -->
      <p>忘了自己的Shop_用户或密码</p>
      <p @click="createID">没有Shop_用户立即创建一个</p>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 获取表单的用户名和密码
      form: {
        name: '',
        paw: ''
      },
      // 用户规则验证
      rules: {
        name: [
          {
            required: true,
            message: 'Please input Activity name',
            trigger: 'blur',
          },
          {
            min: 3,
            max: 12,
            message: 'Length should be 3 to 12',
            trigger: 'blur',
          }

        ],
        paw: [
          {
            required: true,
            message: 'Please input Activity name',
            trigger: 'blur',
          },
          {
            min: 3,
            max: 20,
            message: 'Length should be 3 to 20',
            trigger: 'blur',
          }
        ]
      }
    }
  },
  methods: {
    // 用户登录按钮
    open () {
      // 触发romdenToken()函数
      this.$store.commit('romdenToken')
      //  触发timeToken()函数
      this.$store.commit('timeToken', 1)
      //   接收window.localStorage.user
      if (window.localStorage.user == undefined) return this.$message({
        message: '请注册账号，谢谢！',
        type: 'success'
      })
      let user
      user = JSON.parse(window.localStorage.user)
      try {
        user.forEach((e, index) => {
          if (e.name == this.form.name) {
            if (e.paw == this.form.paw) {
              //   window.document.cookie = `token=${this.$store.state.Token};expires=${this.$store.state.xtime}`
              window.sessionStorage.Token = this.$store.state.Token
              console.log(e)
              this.$router.push('/Main')
              throw Error()
            } else {
              this.$message({
                message: '密码错误',
                type: 'error',
              })
            }

          }
          if (user.length == index + 1) {
            this.$message({
              message: '用户名错误',
              type: 'error'
            })
          }
        });
      } catch (e) {
        this.$message({
          message: '登录成功',
          type: 'success'
        })
      }



    },

    // 转到创建用户页面
    createID () {
      this.$router.push('/CreateID')
    }

  },
};
</script>

<style lang="scss">
.login {
  background-color: skyblue;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .loginview {
    width: 400px;
    height: 500px;
    border: 1px #666 solid;
    border-radius: 20px;
    box-shadow: 4px 4px 10px #888;
    background-color: #f2f2f2;

    h1 {
      color: #363636;
      margin: 10px 10px 80px 10px;
    }
    p {
      color: #1d79d4;
      height: 35px;
      line-height: 35px;
      margin-left: 40px;
    }
    p:hover {
      cursor: pointer;
      color: pink;
    }
    .login-space {
      height: 50px;
    }
  }
}

.el-form-item {
  width: 320px;
  .el-input__inner {
    border-radius: 6px;
  }
}
</style>

