<template>
  <div class="login">
    <div class="loginview">
      <h1>注册页面。</h1>
      <!-- 登录表单 -->
      <el-form
        ref="form"
        :model="form"
        :rules="rules"
        label-width="120px"
        class="demo-ruleForm"
      >
        <!-- 用户名 -->
        <el-form-item label="Shop_用户" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item label="Shop_密码" prop="paw">
          <el-input
            v-model="form.paw"
            placeholder="Please input password"
            show-password
          />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="open('form')">Create</el-button>
          <el-button>Cancel</el-button>
        </el-form-item>
      </el-form>
      <div class="login-space"></div>
      <!-- 创建用户 -->
      <p @click="onlogin">转到登录页面</p>
      <p>没有Shop_用户立即创建一个</p>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 获取表单的用户名和密码
      form: {
        name: '',
        paw: ''
      },
      // 用户规则验证
      rules: {
        name: [
          {
            required: true,
            message: 'Please input Activity name',
            trigger: 'blur',
          },
          {
            min: 3,
            max: 12,
            message: 'Length should be 3 to 12',
            trigger: 'blur',
          }

        ],
        paw: [
          {
            required: true,
            message: 'Please input Activity name',
            trigger: 'blur',
          },
          {
            min: 3,
            max: 20,
            message: 'Length should be 3 to 20',
            trigger: 'blur',
          }
        ]
      }
    }
  },


  methods: {
    // 用户登录按钮
    open (form) {
      // 接收localStorage.user数据
      let user
      // 接收window.localStorage
      let local
      // 验证表单   
      this.$refs[form].validate((valid) => {

        local = window.localStorage
        if (local.user == undefined) {
          local.user = JSON.stringify([{ name: this.form.name, paw: this.form.paw }, { name: this.form.name, paw: this.form.paw }])
        } else {
          user = JSON.parse(local.user)
          if (user.length == undefined) {
            user = [user]

          }
          user.push({ name: this.form.name, paw: this.form.paw })
          local.user = JSON.stringify(user)
        }

        if (valid) {
          this.$message({
            message: '注册成功',
            type: 'success'
          })
        } else {
          this.$message({
            message: '账号或者密码不符合规范，请重新注册',
            type: 'error'
          })
          return false
        }
      })
    },

    // 转到登录页面
    onlogin () {
      this.$router.push('/Login')
    }
  },
};
</script>

<style lang="scss">
.login {
  background-color: skyblue;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .loginview {
    width: 400px;
    height: 500px;
    border: 1px #666 solid;
    border-radius: 20px;
    box-shadow: 4px 4px 10px #888;
    background-color: #f2f2f2;

    h1 {
      color: #363636;
      margin: 10px 10px 80px 10px;
    }
    p {
      color: #1d79d4;
      height: 35px;
      line-height: 35px;
      margin-left: 40px;
    }
    p:hover {
      cursor: pointer;
      color: pink;
    }
    .login-space {
      height: 50px;
    }
  }
}

.el-form-item {
  width: 320px;
  .el-input__inner {
    border-radius: 6px;
  }
}

html,
body,
#app,
#nav {
  width: 100%;
  height: 100%;
}

* {
  padding: 0;
  margin: 0;
}
</style>