import { createStore } from 'vuex'

export default createStore({
    state: {
        Token: '',
        xtime: 0,
        ipUrl: 'http://[2409:8a55:d4d1:7910:2c6b:e850:a6ea:30ba]:3000/view/image/'
    },
    mutations: {
        romdenToken (state) {
            state.Token = parseInt(Math.random() * 10000000000)
        },
        timeToken (state, n) {
            let xtime = new Date();
            let mtime = new Date(xtime.getTime() + n * 60 * 60 * 24 * 1000)
            state.xtime = mtime.toGMTString()


        }
    },
    actions: {
    },
    modules: {
    }
})
